<template>
  <div style="position: absolute;top: 40%; left: 50%; margin-left: -100px; margin-top: -40px">
    <el-row type="flex" class="row-bg" justify="center">
      <div id="upload">
        <el-upload
          class="upload-demo"
          action="/api/files/upload"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          :limit="23"
          multiple
          :on-exceed="handleExceed"
          :file-list="fileList"
          :on-success="handleSuccess"
          :on-change="init">
          <el-button round size="medium" type="primary" style="width: 200px; height: 60px;">点击上传</el-button>
        </el-upload>
        <el-button class="next" type="primary" v-show="show" @click="gotoIndexing">下一步<i
          class="el-icon-arrow-right el-icon--right"></i></el-button>
      </div>
    </el-row>
  </div>
</template>
<script>
  import Indexing from './indexing'

  export default {
    components: {
      Indexing
    },
    mounted() {
      this.init()
    },
    data() {
      return {
        fileList: [],
        show: false
        // fileList: [{
        //   name: 'food.jpeg',
        //   url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
        // }, {
        //   name: 'food2.jpeg',
        //   url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
        // }]
      };
    },
    methods: {
      init() {
        var that = this
        this.$axios.get('/api/files/list', {})
          .then(function (response) {
            console.log(response.data)
            that.fileList = response.data
            console.log(that.fileList.length)
            if (that.fileList.length != 0) {
              that.show = true
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      handleRemove(file, fileList) {
        console.log(file, fileList)
        this.$axios.get('/api/files/remove', {
          params: {
            filename: file.name
          }
        })
          .then(function (response) {
            console.log(response);
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      handlePreview(file) {
        console.log(file)
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 23 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`)
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${file.name}？`)
      },
      handleSuccess(response, file, fileList) {
        console.log(response.toString())
        this.show = true
      },
      gotoIndexing() {
        this.$emit('say', 1);
        this.$router.push('/indexing');
      },

    }
  }
</script>
<style scoped>
  .next {
    margin-top: 20px;
  }

  .slide-fade-enter-active {
    transition: all 1.6s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */
  {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



