import Vue from 'vue'
import Router from 'vue-router'
import Search from '../components/Search'
import Indexing from '../components/indexing'
import Upload from '../components/UpLoad'
import List from '../components/List'

Vue.use(Router)

export default [
  {
    path: '/',
    name: Upload,
    component: Upload
  },
  {
    path: '/search',
    component: Search
  },
  {
    path: '/indexing',
    component: Indexing
  },
  {
    path: '/list',
    name: 'List',
    component: List
  }
]



