<template>

  <div id="search">

    <br/>
    <br/>
    <el-input
      placeholder="请输入内容"
      prefix-icon="el-icon-search"
      v-model="message" style="width: 40%">
    </el-input>
    <br/>
    <br/>
    <el-button class="next" type="primary" icon="el-icon-arrow-left" @click="previous">上一步</el-button>
    <el-button icon="el-icon-search" type="primary" @click="search">搜索</el-button>
  </div>

</template>
<script>

  export default {
    data() {
      return {
        message: ""
      }
    },
    methods: {
      previous() {
        this.$emit('say', 1);
        this.$router.push('/indexing')
      },
      search() {
        var data = {
          word: this.message
        }
        var that = this
        this.$axios({
          method: 'post',
          url: '/api/search',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          data: this.$qs.stringify(data),
        })
          .then(function (response) {
            that.$emit('say', 3)
            console.log(response.data)
            that.$router.push({
                name: 'List',
                params: {
                  data: response.data
                }
              })
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    }
  }
</script>

<style scoped>
</style>



