<template>
  <div id="step">
    <el-steps :active="active" finish-status="success" simple style="margin-top: 20px">
      <el-step title="上传"></el-step>
      <el-step title="索引"></el-step>
      <el-step title="搜索"></el-step>
    </el-steps>
  </div>
</template>

<script>
  export default {
    props: ['active'],
    data() {
      return {};
    },

    methods: {
      next() {
        if (this.active++ > 2)
          this.active = 0;
      }
    }
  }
</script>

<style scoped>

</style>



