<template>
  <div style="position: absolute;top: 40%; left: 50%; margin-left: -100px; margin-top: -40px">
    <el-row type="flex" class="row-bg" justify="center">
      <div id="indexing">
        <div v-loading="loading" element-loading-text="拼命加载中">

          <el-button round size="small" type="primary" v-on:click="message" style="width: 200px; height: 60px;">
            {{index}}
          </el-button>

          <br/>
          <el-button class="next" type="primary" icon="el-icon-arrow-left" @click="previous">上一步</el-button>
          <el-button class="next" type="primary" @click="nextone">下一步<i
            class="el-icon-arrow-right el-icon--right"></i></el-button>
          <!--<el-progress type="circle" :percentage="100" status="success" width="40" stroke-width="2"></el-progress>-->
        </div>
      </div>
    </el-row>
  </div>
</template>

<script>
  import {Notification} from 'element-ui';

  export default {
    data() {
      return {
        index: "建立索引",
        loading: false
      }
    },
    methods: {
      message: function () {
        var that = this
        this.loading = true
        this.index = "重新建立索引"
        this.$axios.post('/api/indexing', {})
          .then(function (response) {
            console.log(response)
            that.loading = false
            Notification.success("索引建立成功")
            that.show = true
          })
          .catch(function (error) {
            console.log(error)
          })
      },
      previous() {
        this.$emit('say', 0);
        this.$router.push('/')
      },
      nextone() {
        this.$emit('say', 2);
        this.$router.push('/search')
      }
    }

  }
</script>

<style scoped>
  .next {
    margin-top: 20px;
  }

  .slide-fade-enter-active {
    transition: all 1.2s ease;
  }

  .slide-fade-leave-active {
    transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */
  {
    transform: translateX(10px);
    opacity: 0;
  }

  .item {
    margin-top: 10px;
    margin-right: 40px;
  }
</style>



