<template>
  <el-container>
    <el-main>
  <div id="app">
    <el-button type="primary" v-show="isTop" icon="el-icon-arrow-left" style="float: left; margin-top: 1.8em"
               @click="previous">再来
    </el-button>
    <el-row type="flex" class="row-bg" :class="{'active':!isTop, 'inactive': isTop }" justify="center">
      <el-col :xs="24" :sm="18" :md="12" :lg="12" :xl="12">
        <step v-bind:active="active"></step>
      </el-col>
      <el-tooltip class="item" effect="dark" content="下载官方使用说明" v-show="!isTop" placement="bottom">
        <el-button type="primary" small icon="el-icon-download" style="float: right; margin-top: 1.8em"
                   @click="downloadFile"></el-button>
      </el-tooltip>
      <el-tooltip class="item" effect="dark" content="下载当前索引" v-show="isTop" placement="bottom">
        <el-button type="primary" small icon="el-icon-download" style="float: right; margin-top: 1.8em"
                   @click="downloadIndex"></el-button>
      </el-tooltip>
    </el-row>

    <div style="margin-top: 20px">
      <keep-alive>
        <router-view @say="say" :msg="msg"></router-view>
      </keep-alive>
    </div>
  </div>
    </el-main>
    <el-footer style="margin-top: 30em">
      <el-row type="flex" class="row-bg" justify="center">
        <div>
      请至<a target="_blank" href="https://github.com/christopherwilsrks/Boolean-Retrieval">GitHub</a>查看源码
        </div>
      </el-row>
    </el-footer>
  </el-container>
</template>

<script>
  import Step from './components/Step'

  export default {
    data: {
      active: 0,
      isTop: false
    },
    components: {
      'step': Step
    },
    methods: {
      say(msg) {
        this.active = msg;
        if (msg === 3) {
          this.isTop = true;
        }
        else if (msg === 0) {
          this.isTop = false
        }
        else {
          this.isTop = false
        }
      },
      previous() {
        this.active = 0
        this.isTop = false
        this.$router.go(0)
        this.$router.push('/')
      },
      downloadFile() {
        window.open('/api/files/download')
      },
      downloadIndex() {
        window.open('/api/files/downloadindex')
      },
    },
    name: 'App'
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 0px;
  }

  .header {
    background-color: #409EFF;
  }

  .fix-height {
    height: 130px;
  }

  .active {
    margin-top: 10em;
  }

  .inactive {
    margin-top: 0.3em;
    margin-bottom: 20px;
  }

</style>



