<template>
  <div id="List">
    <el-row :gutter="12" style="height: 100%">
      <el-col :span="8" style="height: 100%">
        <el-card shadow="hover" style="height: 100%;">
          <div style="margin-top: 5em">
              <el-badge :value="item.count" class="item" v-for="item in tokenList">
                <el-button size="medium">{{ item.docsName }}</el-button>
              </el-badge>
          </div>
        </el-card>
      </el-col>
      <el-col :span="8" style="height: 100%;">
        <el-card shadow="hover" style="height: 100%;">
          <div style="margin-top: 15em">
            <el-button type="success" plain v-for="(tag, index) in dynamicTags" @click="changeToken(tag, index)" style="margin-top: 2em; margin-right: 20px" :loading="_index==index">{{ tag }}</el-button>
          </div>
        </el-card>
      </el-col>
      <el-col :span="8" style="height: 100%;">
        <el-card shadow="hover" style="height: 100%;">
          <doneupload></doneupload>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import DoneUpload from '../components/DoneUpload'

  export default {
    data() {
      return {
        docList: [],
        tokenList: [],
        dynamicTags: [],
        _index: 0,
        inputValue: ''
      }
    },
    mounted() {
      // 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
      this.init()
    },
    components: {
      'doneupload': DoneUpload
    },
    methods: {
      init() {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.data
        // 将数据放在当前组件的数据内
        this.docList = routerParams
        this.tokenList = this.docList[0].tokenInfos

        console.log(this.tokenList);
        for (var i = 0; i < this.docList.length; i++) {
          this.dynamicTags.push(this.docList[i].token)
        }
      },
      changeToken(token, index) {

        for (var i = 0; i < this.docList.length; i++) {
          if (this.docList[i].token === token) {
            this.tokenList = this.docList[i].tokenInfos
          }
        }
        this._index = index;

      }
    },
    name: "List"
  }
</script>

<style scoped>
  #List {
    height: 50em;
  }

  .item {
    margin-top: 20px;
    margin-right: 40px;
  }
</style>



